@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono|VT323&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.team-score {
  box-sizing: border-box;
  margin-left: 20px;
  margin-right: 20px;
  padding: 32px;
  width: 216px;
  border: solid 1px #7585b4;
  border-radius: 4px;
  text-align: center; }
  @media screen and (max-width: 576px) {
    .team-score {
      width: 100%;
      margin: 0;
      padding: 32px 0;
      margin-bottom: 20px; } }

.zombie-pixel {
  display: inline-block;
  margin-right: 2px;
  height: 10px;
  width: 4px;
  background: #ff92d1; }

.human-pixel {
  display: inline-block;
  margin-right: 2px;
  height: 10px;
  width: 4px;
  background: #eff99c; }

.message-board {
  height: 100%; }

.message {
  padding: 16px 8px; }
  .message__body {
    color: #fff;
    margin-bottom: 8px; }
  .message__caption {
    display: flex; }

.comment-box {
  display: flex; }
  .comment-box input[type="text"] {
    color: #fff;
    flex-grow: 1;
    margin-right: 8px;
    padding: 6px;
    background: #222;
    border-radius: 2px;
    border: solid 1px #555;
    width: 197px; }
    .comment-box input[type="text"]:focus, .comment-box input[type="text"]:active {
      outline: none;
      border-color: #eff99c; }
  .comment-box button {
    box-sizing: border-box;
    width: 60px;
    height: 27px;
    border-radius: 4px;
    vertical-align: top;
    background: #444;
    color: white;
    font-weight: 600; }
    .comment-box button:hover {
      border-color: #eff99c;
      color: #eff99c; }

.messages-container {
  padding-top: 32px; }

li {
  margin-bottom: 12px; }

ol {
  padding-left: 20px; }

